.home {
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-columns: 230px 1fr;


    .cover {
        width: 100%;
        height: 100vh;
        top: 0px;
        left: 0px;
        position: fixed;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: 2;
        display: grid;
        place-content: center;

        .companyForm {
            width: 400px;
            background-color: white;
            border-radius: 10px;
            position: relative;
            padding: 20px 30px 20px 30px;

            .btn {
                position: absolute;
                top: 5px;
                right: 5px;
                display: grid;
                place-content: center;
                min-height: 0px;
                height: 30px;
                width: 30px;
                color: #979595;
            }

            .formCom {
                .form-group {
                    margin-top: 15px;
                    font-size: 14px;

                    label {
                        margin-bottom: 5px;
                        font-weight: 500;
                        color: #474747;
                    }

                    input {
                        border-radius: 10px;
                        font-size: 13px;
                        height: 40px;
                    }

                }

                .saveCom {
                    margin-top: 20px;
                    text-align: center;

                    .saveBt {
                        width: 120px;
                        text-transform: none;
                        font-weight: bold;
                        margin: 5px;
                    }

                    .up {
                        background-color: green;
                    }

                    .del {
                        background-color: red;
                    }
                }

            }

        }
    }


    .grid1 {
        padding: 10px;
    }

    .grid2 {
        display: flex;
        flex-direction: column;

        .addBtn {
            width: 95%;
            margin: auto;
            margin-top: 30px;
            text-align: right;

            .btnCompany {
                width: 150px;
                text-transform: none;
                font-weight: bold;
                border-radius: 10px;
            }
        }

        .companyTable {
            width: 100%;
            height: 80vh;
            margin-top: auto;
            padding-bottom: 10px;

            .tableMain {
                width: 95%;
                height: 100%;
                border: 1px dotted black;
                margin: auto;
                border-radius: 10px;
                overflow-y: scroll;

                &::-webkit-scrollbar {
                    display: none;
                }


                .companyBox, .companyTitle {
                    width: 100%;
                    line-height: 50px;
                    border-bottom: 1px dotted #797979;
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr 70px;
                    color: #474747;

                    div {
                        padding-left: 20px;
                        font-size: 14px;
                    }

                    label {
                        font-weight: 500;
                        font-size: 16px;
                    }

                    span {
                        cursor: pointer;
                    }
                }

                .companyTitle{
                    color: black;
                    font-weight: 500;
                }

            }
        }
    }
}