.login {
    width: 100%;
    height: 100vh;
    display: grid;
    place-content: center;
    position: relative;
    background-color: #a6b1a1;

    .formBox {
        width: 95vw;
        max-width: 450px;
        background-color: #FAFAFB;
        padding: 25px;
        border-radius: 8px;
        position: relative;


        .form-group {
            margin-bottom: 25px;
            position: relative;
            font-size: 14px;

            label {
                margin-bottom: 5px;
                font-weight: 500;
            }

            input {
                font-size: 14px;
                height: 40px;

                &::placeholder {
                    color: #71717A;
                }
            }

            section {
                position: absolute;
                top: 0px;
                right: 0px;
                color: #525A66;
                font-weight: 500;
                font-size: 12px;
            }

        }


        .loginBt {
            margin-top: 30px;
            width: 100%;
            background-color: #11881B;
            border: 2px solid #0A5310;
            border-radius: 8px;
            height: 45px;
            text-transform: none;
        }
    }

}