.sideBar {
    width: 100%;
    height: 100%;
    padding-top: 30px;
    border-radius: 10px;
    background: #0F55A7;

    .link {
        color: black;
        width: 80%;
        margin: auto;
        background-color: white;
        line-height: 35px;
        border-radius: 10px;
        padding-left: 15px;
        font-size: 14px;
        font-weight: 500;
        cursor: pointer;
        margin-bottom: 20px;

        &:hover {
            background-color: gainsboro;
        }
    }
}